
*{
  padding: 0;
  margin: 0;
}
html,body{
  height: 100%;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
  /* color: rgb(52, 179, 136); */
}
.page {
  height: calc(100% - 52px - 44px);
  overflow-y: auto;
}
.header{
  height: 44px;
  background: #2c3e50;
}
.header h1{
  line-height: 44px;
  font-size: 16px;
  color: #ffffff;
}
ul{
  list-style: none;
}
.ul-list{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 52px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: end;
      -ms-flex-align: end;
          align-items: flex-end;
  border-top: 1px solid rgb(134, 131, 131);
}
.ul-list li{
  width: calc(100% / 4);
}
.ul-list li:nth-child(3){
  position: relative;
}
.circle{
  position: absolute;
  right: 34%;
  bottom:5.5%;
}
.circle em{
  height: 14px;
  font-size: 10px;
  border-radius: 50%;
  background: #c00;
  color: #fff;
  line-height: 14px;
  text-align: center;
  display: inline-block;
  padding: 0 4px;
  text-indent: 0;
}
.ul-list li i{
  display: block;
  font-size: 22px;
}
.ul-list li a{
  color: #2c3e50;
  text-decoration: none;
}
.hidshow{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #2c3e50;
  z-index: 100;
  text-align: right;
  background: url("http://img.yipic.cn/thumb/32225003/0519ef22/f9ccb959/294aec4d/effect_big_322250030519ef22f9ccb959294aec4d.png") no-repeat;
  background-size: 100% 100%
}
.hidshow img{
  width: 100%;
  height: 100%;
}
.hidshow button{
  margin-right: 10px;
  margin-top: 10px;
  padding: 5px 10px;
  background: rgba(178, 209, 38, 0.1);
  color: #ffffff;
  border:none;
  height: 30px;
  line-height: 23px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  text-align: center;
  border-radius: 15px;
  outline: none;
  letter-spacing: 3px;
}
.no-order{
  margin: 0 auto;
  margin-top: 150px;
}

.swiper-slide img{
    width: 100%;
    height: 200px;
}
.text{
    padding: 8px 10px 10px;
    border-bottom: 1px solid #d3d3d3;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.text i{
    color:yellow;
    font-size: 20px;
}
.text span{
    font-size: 14px;
}
.newbook{
    padding: 15px;
    padding-bottom: 0px;
}
.newbook>p{
    text-align: left;
    font-weight: bolder;
    color: rgb(136, 191, 212);
    margin-bottom: 10px;
}
.book-list{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
.book-list li{
    width: 47%;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    -ms-flex-line-pack: justify;
        align-content: space-between;
    margin-bottom: 10px;
}
.book-list li:nth-child(2n-1){
    margin-right: 15px;
}
.book-list li img{
    width: 140px;
    height: 190px;
}
.book-list li p{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.small-p{
    font-size: 14px;
    color: rgb(129, 158, 158);
    margin-bottom: 5px;
    margin-top: 8px;
}
.price-p{
    font-size: 13px;
    text-align: left;
    color: rgb(218, 98, 62);
}
